* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Montserrat';
  background-color: #fff;
}

.layout {
  width: 90%;
  margin: 0px auto;

  .navbar {
    display: flex;
    padding: 10px 0px;
    align-items: center;
    justify-content: space-between;
    
    .burger__menu {
      display: inline-block;
      cursor: pointer;
      border: none;
      font-size: 2rem;
    }
  }

  .blog__list {
    margin: 15px 0px 0px 0px;
    .blog__list__item {
      border: 1px solid black;
      border-radius: 5px;
      padding: 0px 10px;
      margin: 15px 0px;
      
      .blog__heading {
        padding: 5px 0px 3px 0px;
        font-size: 1.75rem;
        font-family: 'Poppins';
      }
      
      .blog__desc {
        padding: 10px 0px;
        font-size: 1rem;
      }
      
      .read__more__button {
        display: inline-block;
        border-radius: 5px;
        border: 1px solid black;
        background-color: #f62a3c;
        padding: 10px;
        margin: 5px 0px;
        color: #fff;
        font-size: 1.3rem;
        text-decoration: none;
      }
      
    }
  }
}

.nav__menu {
  height: 100%;
  width: 0;
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  background-color: #111;
  overflow-x: hidden;
  transition: 0.5s;
  padding-top: 60px;
  
  a {
    background-color: transparent;
    padding: 8px 8px 8px 32px;
    text-decoration: none;
    font-size: 2rem;
    color: #818181;
    display: block;
    transition: 0.3s;
  }

  a:hover {
    color: #f1f1f1;
  }

  .closebtn {
    position: absolute;
    top: 0;
    right: 25px;
    font-size: 3rem;
    margin-left: 50px;
    cursor: pointer;
  }
}

.nav__menu__visible {
  width: 100%;
}

.no__scroll {
  overflow: hidden;
}

.blog-container {
  line-height: 1.5;
  .heading {
    padding: 5px 0px 3px 0px;
    font-size: 1.75rem;
    font-family: 'Poppins';
  }

  p {
    margin: 20px 0px;
  }
}

@media screen and (max-height: 450px) {
  .sidenav {padding-top: 15px;}
  .sidenav a {font-size: 1.25rem;}
}